.contacts {
  @media screen and (max-width: $desktop-width - 1) {
    margin-top: -5px;
  }

  @media screen and (max-width: $tablet-width - 1) {
    margin-top: 0;
  }
}

.contacts__list {
  display: flex;
  flex-wrap: wrap;
  min-width: 566px;
  max-width: 65%;
  width: fit-content;
  margin-left: auto;
  margin-right: auto;
  align-items: center;

  @media screen and (max-width: $desktop-width - 1) {
    max-width: 85%;
  }

  @media screen and (max-width: $tablet-width - 1) {
    min-width: 262px;
    padding-top: 4px;
    flex-direction: column;
  }
}

.contacts__item:not(:last-child) {
  margin-right: 88px;

  @media screen and (max-width: $tablet-width - 1) {
    margin-right: auto;
    margin-left: auto;
    margin-bottom: 11px;
  }
}

.contacts__link {
  width: fit-content;
  padding-top: 2px;
  padding-left: 40px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  color: rgba(0, 0, 0, 0.5);

  &::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 0;
    width: 26px;
    height: 21px;
    transform: translateY(-50%);
    background-repeat: no-repeat;
  }

  &--mail::before {
    background-image: url("../img/icon-mail.svg");
  }

  &--phone::before {
    background-image: url("../img/icon-phone.svg");
    height: 25px;
  }

  &:hover,
  &:focus {
    color: $text-accent;
  }

  &:active {
    opacity: 0.7;
  }

  @media screen and (max-width: $tablet-width - 1) {
    &--phone {
      margin-left: -21px;
    }
  }
}
